  Tooltip,
  Legend,
  PieController
} from 'chart.js';

// Register Chart.js components
ChartJS.register(ArcElement, Tooltip, Legend, PieController);

export default {
  name: 'ExpenseTracker',  setup() {    const transactions = ref([]);
