    const startRecording = async () => {
      try {
        voiceError.value = '';
        
        // Request microphone access
        const stream = await navigator.mediaDevices.getUserMedia({ 
          audio: {
            echoCancellation: true,
            noiseSuppression: true,
            sampleRate: 44100
          } 
        });
        
        mediaStream.value = stream;
        
        // Create recorder
        recorder.value = new RecordRTC(stream, {
          type: 'audio',
          mimeType: 'audio/wav',
          recorderType: RecordRTC.StereoAudioRecorder,
          numberOfAudioChannels: 1,
          desiredSampRate: 16000,
          bufferSize: 4096
        });
        
        recorder.value.startRecording();
        isRecording.value = true;
        recordingTime.value = 0;
        
        // Start timer
        recordingTimer.value = setInterval(() => {
          recordingTime.value++;
          if (recordingTime.value >= 60) { // Auto-stop after 60 seconds
            stopRecording();
          }
        }, 1000);
        
      } catch (error) {
        voiceError.value = 'Microphone access denied. Please enable microphone permissions.';
        console.error('Error starting recording:', error);
      }
    };

    const stopRecording = () => {
      if (!recorder.value || !isRecording.value) return;
      
      isRecording.value = false;
      isProcessing.value = true;
      
      // Clear timer
      if (recordingTimer.value) {
        clearInterval(recordingTimer.value);
        recordingTimer.value = null;
      }
      
      recorder.value.stopRecording(() => {
        const blob = recorder.value.getBlob();
        
        // Stop media stream
        if (mediaStream.value) {
          mediaStream.value.getTracks().forEach(track => track.stop());
        }
        
        // Process the audio blob
        processAudioBlob(blob);
      });
