        const params = exportRange.value === 'daily'
          ? `?export_type=daily`
          : `?range=${exportRange.value}&start=${format(startDate)}&end=${format(endDate)}`;        const response = await fetch(`http://192.168.1.113/expenseNote/api/export_pdf.php${params}`, {
